<template>
  <el-row :gutter="20">
    <el-col :span="18" :offset="3">
      <p style="margin-bottom: 50px;line-height: 150%;">
        <a href="https://github.com/zhufuyi/sponge" target="_blank">sponge</a>
        是一个集成了 <span class="key-word">自动生成代码</span>、<span
          class="key-word"
          >gin和grpc</span
        > 的go语言基础开发框架。sponge拥有丰富的生成代码命令，生成不同的功能代码可以组合成完整的服务(类似人为打散的海绵细胞可以自动重组成一个新的海绵)。代码解耦模块化设计，很容易构建出从开发到线上部署的完整工程项目，开发web或微服务项目轻而易举、事半功倍，使用go也可以"低代码开发"。
      </p>
      <p>
        {{ $t("home.text2")
        }}<a href="https://github.com/zhufuyi/sponge" target="_blank"
          >https://github.com/zhufuyi/sponge</a
        >
      </p>
      <p>
        {{ $t("home.text3")
        }}<a href="https://go-sponge.com/zh-cn/" target="_blank"
          >https://go-sponge.com/zh-cn/</a
        >
      </p>
      <p>
        {{ $t("home.text3_1")
        }}<a
          href="https://space.bilibili.com/1440482691/channel/series"
          target="_blank"
          >https://space.bilibili.com/1440482691/channel/series</a
        >
      </p>
      <br />
      <p>{{ $t("home.text4") }}</p>
      <div class="home-image">
        <el-image :src="homeImageSrc"></el-image>
      </div>
      <br />
      <p>{{ $t("home.text5") }}</p>
      <div class="microservices-image">
        <el-image :src="mircoServiceFrameworkImageSrc"></el-image>
      </div>
      <br />
      <p></p>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      homeImageSrc: require("@/assets/sponge-framework.png"),
      mircoServiceFrameworkImageSrc: require("@/assets/microservices-framework.png"),
    };
  },
};
</script>

<style>
.home-image {
  margin: auto;
}

.microservices-image {
  width: 70%;
  margin: auto;
}

.key-word {
  /* background-color: #ddd; */
  color: #c7254e;
}
</style>



