<template>
  <el-row :gutter="20">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>
          <span style="color: #89ae06">⓶Create grpc service based on sql</span>
          <span style="color: #777; font-weight:normal;">
            generate complete microservices project code</span
          >
        </h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="database" prop="dbDriver">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.dbDriver"
              placeholder="please select database type"
              clearable
              filterable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-option
                v-for="(item, index) in dbDriverNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                After selecting the database type, the generated code will
                automatically match that database type, where tidb is compatible
                with mysql.
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="database dsn" prop="dsnAddr">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.dsnAddr"
              placeholder="user:password@(127.0.0.1:3306)/db_name"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                If the database is <b>msyql</b>, <b>postgresql</b>,
                <b>tidb</b> fill in the dsn address.<br />
                Example：<b>user:password@(host:port)/db_name</b><br /><br />
                If the database is <b>sqlite</b>, fill in the local absolute
                path of the sqlite db file.<br />
                Example: <b>/tmp/test.db</b> or
                <b>C:\Users\Desktop\test.db</b>
              </div>
            </el-tooltip>
            <el-link
              style="margin-left: 10px; font-size: 16px;"
              type="primary"
              @click="listTables(true)"
              >get tables</el-link
            >
          </el-form-item>
          <el-form-item label="table name" prop="tableName">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.tableName"
              placeholder="multi-selection support"
              clearable
              filterable
              multiple
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-option
                v-for="(item, index) in tableNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                If you need to add new CRUD code in the future,<br />
                after which you can generate code in the menu<br />
                <b>[Public] -> [generate service CRUD code]</b>,<br />
                and then moved to the project code.
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="service name" prop="serverName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.serverName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                If the name contains hyphens, they will be converted to
                underscores.<br />
                example: your-service 🡆 your_service
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="module name" prop="moduleName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.moduleName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                module name for go.mod file.
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="project name" prop="projectName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.projectName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                If the name contains uppercase letters, they will be<br />
                converted to lowercase and separated by underscores.<br />
                example: yourProject 🡆 your-project
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="docker repository address">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.repoAddr"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Image repository address, the default value is
                image-repo-host,<br />
                this parameter is used to build docker images, k8s deployment<br />
                scripts, if you use the official docker image repository,
                just<br />
                fill in the user name of the registered docker repository, if<br />
                you use the private repository address, you need to fill in
                the<br />
                full repository address.
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="embed model">
            <el-switch v-model="ruleForm.embed"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                The gorm.Model struct field corresponds to the four column names
                <br />
                id, created_at, updated_at, and deleted_at in the MySQL table.
                <br />
                If the MySQL table contains these column names, enable it.<br />
                If the MySQL table does not contain these column names, disable
                it.
              </div>
            </el-tooltip>
          </el-form-item>
          <div style="margin: 30px auto;"></div>
        </el-form>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">Reset</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            >Download code</el-button
          >
          <DownloadCode ref="downloadCode"></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/en/DownloadCode";
export default {
  name: "MicroRPC",
  data() {
    return {
      title: "",
      path: "micro-rpc",
      helpInfo: `
      <p><b>⓶Create grpc service based on sql</b> includes the functions required for development and deployment, including the api interface for adding, deleting, modifying and querying mysql tables and grpc client test code. Simply define the api interface description information in the proto file and then write the specific logic code in the generated template code.</p>
      <br />
      <p>1. unzip the code file.</p>
      <p>2. open a terminal and execute the command to generate API template code, API error code, grpc client test code, and * pb.go code:  <b style="color: green">make proto</b></p>
      <p>3. compile and run services:   <b style="color: green">make run</b></p>
      <p>4. open the file using <b>Goland</b> or <b>VS Code</b> <span style="color: chocolate">internal/service/xxx_client_test.go</span>, test or pressure test the api interface.</p>
      `,

      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,

      ruleForm: {
        dsnAddr: "",
        tableName: [],
        dbDriver: "",
        moduleName: "",
        serverName: "",
        projectName: "",
        repoAddr: "",
        embed: false,
      },
      rules: {
        dsnAddr: [
          {
            required: true,
            message: "please type the mysql dsn address",
            trigger: "blur",
          },
        ],
        tableName: [
          {
            required: true,
            message: "please select a table name",
            trigger: "change",
          },
        ],
        dbDriver: [
          {
            required: true,
            message: "please select a database type",
            trigger: "change",
          },
        ],
        moduleName: [
          {
            required: true,
            message: "please type the name of the go module",
            trigger: "blur",
          },
        ],
        serverName: [
          {
            required: true,
            message: "please type the name of the service",
            trigger: "blur",
          },
        ],
        projectName: [
          {
            required: true,
            message: "please type the project name",
            trigger: "blur",
          },
        ],
      },

      tableNames: [],
      dbDriverNames: [
        { label: "mysql", value: "mysql" },
        { label: "postgresql", value: "postgresql" },
        { label: "tidb", value: "tidb" },
        { label: "sqlite", value: "sqlite" },
      ],
    };
  },
  created: function() {
    this.adaptiveWindows();
    this.listDbDrivers();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.dsnAddr = data.data.dsn;
          this.ruleForm.dbDriver = data.data.dbDriver;
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.serverName = data.data.serverName;
          this.ruleForm.projectName = data.data.projectName;
          this.ruleForm.repoAddr = data.data.repoAddr;
          // this.ruleForm.embed = data.data.embed;

          this.listTables(false);
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const apiUrl = appConfig.spongeServiceAddr + "/generate";
          var selectTableNames = this.ruleForm.tableName.join(",");
          const arg = `micro rpc --module-name=${this.ruleForm.moduleName} --server-name=${this.ruleForm.serverName} --project-name=${this.ruleForm.projectName} --repo-addr=${this.ruleForm.repoAddr} --db-driver=${this.ruleForm.dbDriver} --db-dsn=${this.ruleForm.dsnAddr} --db-table=${selectTableNames} --embed=${this.ruleForm.embed}`;
          this.$refs.downloadCode.execution(
            apiUrl,
            this.path,
            arg,
            this.helpInfo,
          );
        } else {
          return false;
        }
      });
    },
    clearFormData() {
      this.tableNames = [];
      this.ruleForm = {
        tableName: [],
        embed: false,
      };
    },
    listTables(isTip) {
      if (this.ruleForm.dsnAddr == "") {
        return;
      }
      const apiUrl = appConfig.spongeServiceAddr + "/listTables";
      const formdata = {
        dsn: this.ruleForm.dsnAddr,
        dbDriver: this.ruleForm.dbDriver,
      };
      axios
        .post(apiUrl, formdata, { timeout: 3000 })
        .then(res => {
          const { data } = res;
          if (isTip) {
            data.code == 0
              ? this.$message.success("Get table name success")
              : this.$message.error("Failed to get table name");
          }

          this.tableNames = data.data;
        })
        .catch(err => {
          if (isTip) {
            this.$message.error(
              "Failed to get table name, please check if " +
                this.ruleForm.dbDriver +
                "  dsn address is correct or " +
                this.ruleForm.dbDriver +
                " service is running.",
            );
          }
          // console.log(err);
        });
    },
    listDbDrivers() {
      const apiUrl = appConfig.spongeServiceAddr + "/listDrivers";
      axios
        .get(apiUrl)
        .then(res => {
          const { data } = res;
          if (data.code == 0) {
            this.dbDriverNames = data.data;
          }
        })
        .catch(err => {
          // console.log(err);
        });
    },
    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "(other)";
        this.$forceUpdate();
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      this.value = val;
      this.$forceUpdate();
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: medium;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}
</style>



